<template>
  <div class="wrap">
    <Header :left="true" title="个人中心" />
    <content class="content">
      <van-grid>
        <van-grid-item>
          <van-image
            width="100"
            height="100"
            src="https://img.yzcdn.cn/vant/cat.jpeg"
          />
        </van-grid-item>
      </van-grid>
    </content>
    <Footer />
  </div>
</template>

<script>
import Cookies from "js-cookie";
export default {
  name: 'Mine',
  data () {
    return {
      msg: '个人中心内容'
    }
  },
  // components:{ Header, Footer },
  methods:{
    checkLogin(){//检查是否登录
      const username=Cookies.get('username');
      if(!username){
        this.$router.push("/login");
        // this.$router.push("/login");
      }
    }
  },
  created:function () {
    this.checkLogin();
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
  .content{
    flex: 1;
    background: #f5f5f5;
    padding:0.4rem;
  }
</style>


