<template>
  <van-dropdown-menu active-color="#ee0a24">
    <van-dropdown-item v-model="value[0]" :options="option[0]" />
    <van-dropdown-item v-model="value[1]" :options="option[1]" />
  </van-dropdown-menu>
</template>
<script>
const option1 = [
  {text:"全部商品",value:0},
  {text:"标准果",value:1},
  {text:"精品果",value:2},
]
const option2 = [
  {text:"默认排序",value:0},
  {text:"价格升序",value:1},
  {text:"价格降序",value:2},
]
export default {
  name: 'Goods',
  data () {
    return {
      value:[0,0],
      msg: '货架导航',
      option:[option1,option2]
    }
  }
}
</script>
<style lang="less" scoped>

</style>


