<template>
  <div class="wrap">
    <Header :left="true" title="商城" />
    <content class="content">
      <div class="filter">
        <Goods />
      </div>
      <div class="list">
        <van-card
          num="999+"
          price="78.00"
          desc="10斤标准果,果径65-75mm"  
          title="10斤标准果"
          thumb="https://img.yzcdn.cn/vant/t-thirt.jpg"
          origin-price="98.00"
          v-for="(item,i) in goods"
          :key="i"
        >
          <div slot="tags">
            <van-tag plain type="danger">标准果</van-tag>
          </div>
          <div slot="footer">
            <van-icon name="cart-circle-o" class="shop_car" />
          </div>
        </van-card>
      </div>
    </content>
    <Footer />
  </div>
</template>

<script>
import Goods from "./list";
export default {
  name: 'Mall',
  data () {
    return {
      msg: '商城内容',
      goods:[0,1,2,3,4,5,6,7,8]
    }
  },
  components:{ Goods }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
  .content{
    flex: 1;
    background: #f5f5f5;
    overflow:auto;
    display: flex;
    flex-direction:column;
    .filter{
      background:orange;
      .van-dropdown-menu{
        border-top-left-radius:10px;
        border-top-right-radius:10px;
      }
      .van-hairline--top-bottom::after{
        display:none;
      }
    }
    .list{
      flex: 1;
      overflow: auto;
    }
  }
</style>


