import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

const store = new Vuex.Store({
  state:{
    tab:"home"
  },
  mutations:{
    saveTab(state,payload){
      state.tab=payload;
    }
  },
  actions:{
    changeTab(content,payload){
      content.commit("saveTab",payload)
    }
  }
});
export default store;


